// Common elements

.title {
  font-family: 'Caveat Brush', cursive;
  text-align: center;
}

.button {
  display: block;
  width: 145px;
  height: 40px;

  font-family: 'Caveat Brush', cursive;
  font-style: normal;
  font-size: 18px;
  line-height: 1.28;
  color: $button-text-color;

  background: $accent-color;

  border: 0px;
  border-radius: 25px;

  margin: 0 auto;

  cursor: pointer;
}

.container {
  max-width: $mobile-second-breakpoint;
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: $tablet-breakpoint) {
    & {
      max-width: $desktop-breakpoint;
      padding: 0 32px;
    }
  }

  @media screen and (min-width: $desktop-breakpoint) {
    & {
      max-width: $desktop-second-breakpoint;
      padding: 0 20px;
    }
  }
}

.active {
  color: $accent-color;
}

// Form

.form__field {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form__input {
  outline: none;

  width: 100%;

  margin-bottom: 20px;

  border: none;

  cursor: pointer;

  border-bottom: 1px solid $accent-color;

  &::placeholder {
    font-size: 16px;
    line-height: 1.25;
    color: $second-text-color;
  }
}

// Visually-hidden

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
